import { faArrowLeft, faBackspace } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";

export const MainHelp = () => {
	const informacion = ["Entidades", "Atributos", "Relaciones", "Herencia", "Ejemplo"];
	const [openInfo, setopenInfo] = useState("");

	const Back = () => {
		return (
			<div>
				<button
					onClick={() => {
						setopenInfo("");
					}}
					className="border-gray-200 rounded-md border-2 p-1"
				>
					<FontAwesomeIcon icon={faArrowLeft} /> VOLVER
				</button>
			</div>
		);
	};
	const informationOfHelp = (elemento: any) => {
		switch (elemento) {
			case "Entidades":
				return (
					<div>
						<Back />
						<div className="text-2xl text-center py-2">Entidades</div>
						<div className="py-2">Las entidades representan clases de objetos de la realidad</div>
						<div className="py-2">El siguiente es un ejemplo de la entidad Producto con </div>
						<div className="py-2 whitespace-pre-line">
							{`ENTITY producto{
							codigo IDENTIFIER,
							descripcion
							}`}
						</div>
					</div>
				);
			case "Relaciones":
				return (
					<div>
						<Back />
						<div className="text-2xl text-center py-2">Relaciones</div>
						<div className="py-2">Las relaciones representan agregaciones de dos o ms entidades</div>
						<div className="py-2">El siguiente es un ejemplo de una relacion entre producto y linea </div>
						<div className="py-2 whitespace-pre-line">
							{`ENTITY Ciudad{
nombre,
codigo_postal IDENTIFIER,
cantidad_habitantes CALCULATED BY 'COUNT(DNI)'
};
RELATIONSHIP VIVE_EN{
Ciudad(1,N),
Persona(1,1)
};
ENTITY Persona{
nombre,
apellido,
DNI IDENTIFIER
}`}
						</div>
					</div>
				);
			case "Atributos":
				return (
					<div>
						<Back />
						<div className="text-2xl text-center py-2">Atributos</div>
						<div className="py-2">representan las propiedades bsicas de las entidades o relaciones</div>
						<div className="py-2">El siguiente es un ejemplo de persona con atributos simples calculados y compuestos</div>
						<div className="py-2 whitespace-pre-line">
							{`
							COMPOSITE domicilio{
								ciudad,
								calle,
								numero
							};
                            ENTITY persona{
                                documento IDENTIFIER,
                                cuil IDENTIFIER,
                                nacimiento,
                                edad CALCULATED BY 'HOY - nacimiento',
                                nombre,
                                celular (0,1),
                                domicilio
                            }
                            `}
						</div>
					</div>
				);
			case "Herencia":
				return (
					<div>
						<Back />
						<div className="text-2xl text-center py-2">Herencia</div>
						<div className="py-2">Una abstraccin de generalizacin establece una correspondencia entre la clase genrica "(raz)" y las clases subconjunto.</div>
						<div className="py-2">El siguiente es un ejemplo de herencia para la clase persona.</div>
						<div className="py-2 whitespace-pre-line">
							{`
                            ENTITY persona COVERAGE (PARTIAL, OVERLAPPING){
                                documento IDENTIFIER,
                                cuil IDENTIFIER,
                                nacimiento,
                                edad CALCULATED BY 'HOY - nacimiento',
                                nombre,
                                celular (0,1),
                                domicilio
                            };
                            
                            ENTITY proveedor CHILD OF persona{
                                cuit IDENTIFIER 
                            };
                            ENTITY proveedorer CHILD OF persona{
                                cuit IDENTIFIER 
                            }
                            `}
						</div>
					</div>
				);
			case "Ejemplo":
				return (
					<div>
						<Back />
						<div className="text-2xl text-center py-2">Ejemplo</div>
						<div className="py-2">El ejemplode abajo es un modelo tpico de factura</div>
						<div className="py-2">podes copiarlo y pegarlo en la aplicacion</div>
						<div className="py-2 whitespace-pre-line">
							{`
                        COMPOSITE domicilio{
							ciudad,
							calle,
							numero
						};
						
						ENTITY persona COVERAGE (PARTIAL, OVERLAPPING){
							documento IDENTIFIER,
							cuil IDENTIFIER,
							nacimiento,
							edad CALCULATED BY 'HOY - nacimiento',
							nombre,
							celular (0,1),
							domicilio
						};
						
						ENTITY proveedor CHILD OF persona{
							cuit IDENTIFIER 
						};
						
						ENTITY empleado CHILD OF persona{
							legajo IDENTIFIER
						};
						
						ENTITY factura{
							tipo IDENTIFIER,
							numero,
							fecha,
							total CALCULATED BY 'SUM(linea.subtotal)'
						};
						
						RELATIONSHIP atendio{
							factura (1, 1),
							empleado (1, N)
						};
						
						RELATIONSHIP compro{
							factura (1, 1),
							persona (1, N)
						};
						
						ENTITY linea WEAK OF factura{
							numero IDENTIFIER,
							precio,
							cantidad,
							subtotal CALCULATED BY 'precio * cantidad'
						};
						
						ENTITY producto{
							codigo IDENTIFIER,
							descripcion
						};
						
						RELATIONSHIP tiene{
							linea (1, 1),
							producto (1, N)
						};
						
						RELATIONSHIP provee{
							producto (1, N),
							proveedor (1, N)
						};
						RELATIONSHIP pertenece{
							linea (1, 1),
							factura (1, N)
						}
                            `}
						</div>
					</div>
				);
			default:
				break;
		}
	};
	return (
		<div className="w-full">
			{!!!openInfo &&
				informacion.map((element) => (
					<div
						className="text-2xl text-gray-200 py-2 px-4 cursor-pointer"
						key={element}
						onClick={() => {
							setopenInfo(element);
						}}
					>
						<div>{element}</div>
					</div>
				))}
			{!!openInfo && <div className="text-gray-200 p-2">{informationOfHelp(openInfo)}</div>}
		</div>
	);
};
